.ico-dinnerplate::before {
  content: '\f101'; }

html, body, p, ol, ul, li, dl, dt, dd, blockquote, figure, fieldset, legend, textarea, pre, iframe, hr, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal; }

ul {
  list-style: none; }

button, input, select, textarea {
  margin: 0; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

img, embed, object, audio, video {
  max-width: 100%;
  height: auto; }

iframe {
  border: 0; }

table {
  border-spacing: 0px;
  border-collapse: collapse; }

td, th {
  padding: 0;
  text-align: left; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

html, * {
  font-family: Myriad-Pro, Helvetica, Arial, sans-serif; }

body {
  margin: 0;
  font-size: 1.6rem;
  line-height: 1.2;
  min-width: 320px;
  -webkit-font-smoothing: antialiased; }

*, *:before, *:after {
  box-sizing: inherit; }

[class^='ico-']:before, [class*=' ico-']:before {
  font-family: Icons; }

.wn-wrapper {
  background-color: red; }



